const sliderDocs = document.querySelector('[data-slider="docs"]');

const docsSlider = () => {
  if (!sliderDocs) {
    return;
  }

  const swiper = new Swiper(sliderDocs, {
    slidesPerView: 3,
    
    autoHeight: true,
    // freeMode: true, 
    spaceBetween: 20,
    navigation: {
      nextEl: '.swiper-button-next.jaw-next',
      prevEl: '.swiper-button-prev.jaw-prev',
    },
    breakpoints: {
      300: {
        slidesPerView: 1,
      },
      767: {
        slidesPerView: 2,
      },
      1150: {
        slidesPerView: 3,
      }
    },
  });
};


export{docsSlider};