const fotoSlider = document.querySelector('[data-slider="foto"]');

const sliderFoto = () => {
  if (!fotoSlider) {
    return;
  }

  const swiper = new Swiper(fotoSlider, {
    slidesPerView: 4,
    
    // autoHeight: true,
    
    spaceBetween: 17,
    navigation: {
      nextEl: '.swiper-button-next.jaw-foto-next',
      prevEl: '.swiper-button-prev.jaw-foto-prev',
    },
    breakpoints: {
        300: {
            slidesPerView: 1,
        },
        390: {
            slidesPerView: 2,
        },

        520: {
            slidesPerView: 3,
        },
        767: {
            slidesPerView: 4,
        },
      
    },
  });
};


export{sliderFoto};