const sliderInjury = document.querySelector('[data-slider="injury"]');

const injurySlider = () => {
  if (!sliderInjury) {
    return;
  }

  const swiper = new Swiper(sliderInjury, {
    slidesPerView: 2,
    
    // autoHeight: true,
    
    spaceBetween: 20,
    navigation: {
      nextEl: '.swiper-button-next.jaw-injury-next',
      prevEl: '.swiper-button-prev.jaw-injury-prev',
    },
    breakpoints: {
        300: {
            slidesPerView: 1,
        },
        
        767: {
            slidesPerView: 2,
        },
      
    },
  });
};


export{injurySlider};